.range-table,
.range-table th,
.range-table td {
  border: 1px solid black;
  border-collapse: collapse;
}

.range-table {
  width: 100%;
  margin-bottom: 10px;
}

.range-table td {
  width: 7.14%; /* 1/14 = 0.0714 */
  position: relative;
}

.range-table td:after {
  content: "";
  display: block;
  margin-top: 100%;
}

.range-table td .content {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.table-header {
  text-align: center;
}
