.raise-button,
.call-button,
.fold-button {
  margin: 2% auto;
  height: 50px;
  width: 30%;
  text-align: center;
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.5;
  border-radius: 0.25rem;
  padding: 0;
}

.raise-button {
  background-color: rgb(255 89 111);
}

.call-button {
  background-color: rgb(159, 217, 113);
}

.fold-button {
  background-color: rgb(0, 146, 239);
}
