#data-container {
  height: 100%;
  width: 100%;
  margin-top: 2%;
}

#data {
  height: 150px;
  width: 100%;
  border-radius: 0.3rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

#result-no-answer {
  margin: auto;
  font-size: 1.3rem;
  color: #ffffff;
}

#judge {
  position: absolute;
  top: -140%;
  left: 36%;
}

#result-raise,
#result-call,
#result-fold {
  width: 33%;
  text-align: center;
  font-size: 1.2rem;
  z-index: 0;
  position: absolute;
  bottom: 5%;
}

#result-raise {
  left: 2%;
}

#result-call {
  left: 33%;
}

#result-fold {
  right: 2%;
}

.icon-circle,
.icon-circle-2,
.icon-circle-3,
.icon-xmark {
  position: absolute;
}

.icon-circle {
  top: -60px;
  left: -65px;
  color: #ffffff;
  font-size: 220px;
}

.icon-circle-2 {
  top: -35px;
  left: -40px;
  color: #ffffff;
  font-size: 170px;
}

.icon-circle-3 {
  top: -50px;
  left: -55px;
  color: rgb(255 89 111);
  font-size: 200px;
}

.icon-xmark {
  top: -90px;
  left: -50px;
  color: #3264ff;
  font-size: 300px;
}

@media screen and (max-height: 812px) {
  #data {
    height: 130px;
  }
}

/* 第二世代iPhoneSE対応 */
@media screen and (max-height: 667px) {
  #data {
    height: 110px;
  }
  #judge {
    top: -180%;
  }
}
