#game {
  height: 100%;
  display: grid;
  flex-direction: column;
}

#game-container,
#board-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

#hand {
  min-width: 320px;
  box-sizing: border-box;
}

#board {
  width: 320px;
  box-sizing: border-box;
  position: relative;
}

#actions {
  width: 100%;
  box-sizing: border-box;
}

#action-button-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
#game-accuracy {
  height: 100%;
  width: 49%;
  font-size: 0.8rem;
  display: grid;
}
#game-accuracy tbody {
  font-size: 1.2rem;
}
#answer-range-button {
  width: 75px;
  height: 50px;
  margin: auto;
  border: 2px solid transparent;
  border-color: #ffffff;
  border-radius: 0.5rem;
}
