h1 {
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 10px;
}
.accuracy-table,
.pod-sum-table {
  width: 100%;
  color: #ffffff;
  padding: 0 10px;
}
.pod-sum-tbody,
.accuracy-tbody {
  text-align: center;
  margin: 2% auto;
  color: rgb(255 89 111);
  line-height: 1.5;
}
#statistics {
  display: flex;
  padding-bottom: 10px;
}
.statistics-container {
  flex: 1;
}
.statistics-container .statistic {
  font-size: 36px;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  letter-spacing: 0.05em;
  font-variant-numeric: proportional-nums;
}
.statistics-container .label {
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
