button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  appearance: none;
}

#close-button {
  /* text-align: right; */
  width: 30px;
  /* float: right; */
  position: absolute;
  top: 10px;
  right: 10px;
}
.usage-title {
  text-align: center;
}
.main-title {
  width: 100px;
  margin: 0 auto;
  font-family: 'Anton', sans-serif;
  letter-spacing: 2px;
}
.sub-title {
  width: 230px;
  margin: 0 auto;
  background: linear-gradient(transparent 70%, yellow 70%);
}
.usage-sentence {
  font-size: 14px;
  padding: 5px 0px;
  line-height: 1.5rem;
}
.en-usage-sentence {
  color: darkslategrey;
  font-size: 14px;
  padding: 5px 0px;
  line-height: 1.5rem;
}

.bottom-sentence {
  border-bottom: solid 1px;
  padding-bottom: 20px;
}

#usage-list {
  padding-left: 15px;
}

.flople {
  font-size: 16px;
}
