#overlay {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#content {
  width: 340px;
  z-index: 2;
  display: grid;
  justify-content: center;
  align-items: center;
}
.game-buttons {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}
.next-game-button {
  width: 75px;
  height: 50px;
  margin: 5% auto;
  color: #fff;
  background-color: #0ca678;
  font-size: 2rem;
  border-radius: 0.5rem;
}
.reset-game-button {
  width: 75px;
  height: 50px;
  margin: 5% auto;
  background-color: #6741d9;
  color: #fff;
  font-size: 2rem;
  border-radius: 0.5rem;
}
#modal-accuracy {
  font-size: 1.2rem;
  color: #ffffff;
  text-align: center;
  margin-bottom: 20px;
}
#modal-accuracy tbody {
  font-size: 2.5rem;
  color: rgb(255 89 111);
}

.accuracy-thead {
  width: 320px;
}
.accuracy-thead tr th {
  padding: 0 10px;
  width: 50%;
}
.sharing-buttons {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 10px;
}
.twitter-button {
  color: #1c9bef;
  margin: 0 auto;
}
